import React from 'react'

function TopSlider() {
    return (
        <div class="splide single-slider slider-no-dots slider-no-arrows slider-visible" id="single-slider-1">
            <div class="splide__track">
                <div class="splide__list">
                    <div class="splide__slide" style={{ width: "100vw" }}>
                        <div class="card card-style m-0 bg-5 shadow-card shadow-card-m" style={{ height: "250px" }}>
                            <div class="card-overlay bg-black opacity-50"></div>
                        </div>
                    </div>
                    <div class="splide__slide" style={{ width: "100vw" }}>
                        <div class="card card-style m-0 bg-9 shadow-card shadow-card-m" style={{ height: "250px" }}>
                            <div class="card-overlay bg-black opacity-50"></div>
                        </div>
                    </div>
                    <div class="splide__slide" style={{ width: "100vw" }}>
                        <div class="card card-style m-0 bg-7 shadow-card shadow-card-m" style={{ height: "250px" }}>
                            <div class="card-overlay bg-black opacity-50"></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default TopSlider